body{background-color:pink
}
.darkB{background-color:rgb(0, 32, 2);padding:50px;margin:40px}
.innerB{border-radius:5px;text-align:center;background-color:rgb(192, 233, 255);padding:20px 50px 50px 50px}
.box{display:flex;flex-wrap:wrap;justify-content:center}
.title{margin-bottom:20px}
.boxes{width:40px;font-size:30px;font-weight:bold;cursor:pointer;box-shadow:0px 3px 3px;border-radius:5px;margin:11px;padding:5px 5px 0px 5px;background-color:rgb(255, 255, 255)}
.diceImage{width:40px;}
button{border-radius:3px;font-weight:bold;cursor:pointer;background-color:rgb(0, 74, 5);width:100px;height:40px;color:white;margin-top:30px}
button:hover{background-color:rgb(1, 127, 10)}
#b10{background-color:blue}

@media all and (max-width:600px){
    .darkB{padding:30px;margin:20px}
    .innerB{padding:10px 30px 30px 30px}
}
@media all and (max-width:470px){
    .darkB{padding:30px;margin:15px}
    .innerB{padding:5px 20px 20px 20px}
}
@media all and (max-width:400px){
    .darkB{padding:10px;margin:10px}
    .innerB{padding:5px 8px 8px 8px}
}
@media all and (max-width:350px){
    .darkB{padding:10px;margin:10px}
    .innerB{padding:0px 10px 10px 10px}
    .boxes{width:40px;margin:7px;padding:4px 4px 0px 4px;}
 .title{margin:-5px 0px 0px}
}
@media all and (max-width:300px){
    .darkB{padding:8px;margin:8px}
    .innerB{padding:0px 6px 6px 6px}
    .boxes{width:30px;height:30px;margin:5px;padding:0px 8px 10px 0px;}
 .title{margin-bottom:0px}
}